	import {
		createRouter,
		createWebHashHistory
	} from 'vue-router'
	import news from '@/views/news/list'
	import forum from '@/views/forum/list'
	import fangyingting from '@/views/fangyingting/list'
	import discussdianyingxinxi from '@/views/discussdianyingxinxi/list'
	import dianyingxinxi from '@/views/dianyingxinxi/list'
	import yonghu from '@/views/yonghu/list'
	import orders from '@/views/orders/list'
	import storeup from '@/views/storeup/list'
	import config from '@/views/config/list'
	import dianyingfenlei from '@/views/dianyingfenlei/list'

export const routes = [{
		path: '/login',
		name: 'login',
		component: () => import('../views/login.vue')
	},{
		path: '/',
		name: '首页',
		component: () => import('../views/index'),
		children: [{
			path: '/',
			name: '首页',
			component: () => import('../views/HomeView.vue'),
			meta: {
				affix: true
			}
		}, {
			path: '/updatepassword',
			name: '修改密码',
			component: () => import('../views/updatepassword.vue')
		}
		
		,{
			path: '/news',
			name: '电影资讯',
			component: news
		}
		,{
			path: '/forum',
			name: '我的发布',
			component: forum
		}
		,{
			path: '/fangyingting',
			name: '放映厅',
			component: fangyingting
		}
		,{
			path: '/discussdianyingxinxi',
			name: '电影信息评论',
			component: discussdianyingxinxi
		}
		,{
			path: '/dianyingxinxi',
			name: '电影信息',
			component: dianyingxinxi
		}
		,{
			path: '/yonghu',
			name: '用户',
			component: yonghu
		}
		,{
			path: '/orders',
			name: '订单管理',
			component: orders
		}
		,{
			path: '/storeup',
			name: '我的收藏',
			component: storeup
		}
		,{
			path: '/config',
			name: '轮播图',
			component: config
		}
		,{
			path: '/dianyingfenlei',
			name: '电影分类',
			component: dianyingfenlei
		}
		]
	},
]

const router = createRouter({
	history: createWebHashHistory(process.env.BASE_URL),
	routes
})

export default router
